





























































































































































































import { Component, Vue } from 'vue-property-decorator'
import { TweenMax, Linear } from 'gsap'
// import outlets from '../assets/img/outlets.png'

@Component({
  components: {}
})
export default class MaintanencePage extends Vue {
  mounted () {
    const itemsDown = [
      '.light4',
      '.light5',
      '.light6',
      '.light7',
      '.light8',
      '.light11',
      '.light12',
      '.light13',
      '.light14',
      '.light15',
      '.light16'
    ]
    itemsDown.forEach(e => this.animateWithRandomNumber(e, -1080, 1080))
    const itemsUp = [
      '.light1',
      '.light2',
      '.light3',
      '.light9',
      '.light10',
      '.light17'
    ]
    itemsUp.forEach(e => this.animateWithRandomNumber(e, 1080, -1080))
  }

  data () {
    return {
      outlets: require('@/assets/img/outlets.png')
    }
  }

  animateWithRandomNumber (myClass:string, from:number, to:number) {
    TweenMax.fromTo(
      myClass,
      Math.floor(Math.random() * 20 + 1),
      { y: from },
      {
        y: to,
        onComplete: this.animateWithRandomNumber,
        onCompleteParams: [myClass, from, to],
        ease: Linear.easeNone
      }
    )
  }
}
