import Vue from 'vue'
import Router from 'vue-router'

// Views
import HomePage from '@/views/HomePage.vue'
import MaintanencePage from '@/views/MaintanencePage.vue'
import NotFound from '@/views/NotFound.vue'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '', redirect: '/home' },
    { path: '/home', name: 'home-page', component: MaintanencePage },
    // { path: '/maintanence', name: 'maintanence-page', component: MaintanencePage },
    { path: '*', name: 'not-found', component: NotFound }
  ]
})
